const categoryReducer = (state = [], action) => {
    switch(action.type) {
        case "CATEGORY1":
            return state = [1,""];
        case "CATEGORY2":
            return state = [2,""];
        case "CATEGORY3":
            return state = [3,""];
        case "CATEGORY4":
            return state = [4,""];
        case "CATEGORY5":
            return state = [5,""];
        default:
            return state;
    }
}

export default categoryReducer;